import opg from 'ts/opg.ts';
import Table from 'ts/ui/Table.ts';
import PopUp from 'ts/ui/Popup';

import {api} from '/ts/util/api';

api({
	'add!post': 'focusonmeetinginfo/AddFocusOnMeetingInfo',
});

export default class AddMatchMeeting {
	constructor(lng: any, srcTb: Table) {
		const $form = $(`<div>
							<table class="search-table">
								<tr>
									<td class="lead"><label for="name">${lng.meetingName}</label></td>
									<td><input id="name" name="name" type="text"/></td>
									<td class="lead"><label for="address">${lng.place}</label></td>
									<td><input id="address" name="address" type="text"/></td>
								</tr>
								<tr>
									<td class="lead"><label for="begindate">${lng.startTime}</label></td>
									<td><input id="begindate" name="begindate" type="datetime" style="width: 100%"/></td>
									<td class="lead"><label for="enddate">${lng.endTime}</label></td>
									<td><input id="enddate" name="enddate" type="datetime" style="width: 100%"/></td>
								</tr>
							</table>
						</div>`);

		let pop: PopUp = opg($form).popup({
			title: lng.add,
			width: 600,
			height: 200,
			buttons: {
				ok: {
					text: lng.add,
					className: 'btn-success',
					onClick: function () {
						let param = $form.fieldsToJson({
							name: {
								type: 'string' ,
								require: true ,
							},
							address: {
								type: 'string' ,
								require: true ,
							},
							begindate: {
								type: 'string' ,
								require: true ,
							},
							enddate: {
								type: 'string' ,
								require: true ,
							},
						});

						if(param){
							api.add(param, res => {
								srcTb.update();
								pop.close();
							});
						}


						return true;
					},
				},
			},
		});

		$('#begindate', $form).datetimepicker({
			timepicker: true,
			step: 30,
			closeOnDateSelect: false,
			format: 'Y-m-d H:i:00',
		});

		$('#enddate', $form).datetimepicker({
			timepicker: true,
			step: 30,
			closeOnDateSelect: false,
			format: 'Y-m-d H:i:00',
		});

	}
}